import React from "react";
import { displayNamesVersion } from "../../ColumnDisplayName";
import { Box, Table, TableBody, TableCell, TableRow } from "@mui/material";
import FiberManualRecordIcon from "@mui/icons-material/FiberManualRecord";
import { Link } from "react-router-dom";

const VersionDetails = ({ version, text }) => {
  const data = [
    {
      header: Object.entries(displayNamesVersion)[0][1],
      body: version.id,
    },
    {
      header: Object.entries(displayNamesVersion)[1][1],
      body: version.version_id,
    },
    {
      header: Object.entries(displayNamesVersion)[2][1],
      body: version.version_uri,
    },
    {
      header: Object.entries(displayNamesVersion)[3][1],
      body: version.char_length,
    },
    {
      header: Object.entries(displayNamesVersion)[4][1],
      body: version.tok_length,
    },
    {
      header: Object.entries(displayNamesVersion)[5][1],
      body: "GitHub URL",
      link: version.url,
    },
    {
      header: Object.entries(displayNamesVersion)[6][1],
      body: version.editor ? version.editor : "No info Available ",
    },
    {
      header: Object.entries(displayNamesVersion)[7][1],
      body: version.edition_place
        ? version.edition_place
        : "No info Available ",
    },
    {
      header: Object.entries(displayNamesVersion)[8][1],
      body: version.edition_date ? version.edition_date : "No date Available ",
    },
    {
      header: Object.entries(displayNamesVersion)[9][1],
      body: version.edition_info ? version.edition_info : "No info Available ",
    },
    {
      header: Object.entries(displayNamesVersion)[10][1],
      body: version.version_lang,
    },
    {
      header: Object.entries(displayNamesVersion)[11][1],
      body: text.tags.split("::").join(";"),
    },
    {
      header: Object.entries(displayNamesVersion)[12][1],
      body: (
        <Box display={"flex"} alignItems={"center"}>
          <FiberManualRecordIcon
            fontSize="small"
            color={version.status}
            aria-label="Annotation Status"
          />
          {version.status}
        </Box>
      ),
    },
    {
      header: Object.entries(displayNamesVersion)[13][1],
      body: (
        <Box display={"flex"} alignItems={"center"}>
          <FiberManualRecordIcon
            fontSize="small"
            color={version.annotation_status}
            aria-label="Annotation Status"
          />
          {version.annotation_status}
        </Box>
      ),
    },
  ];

  return (
    <Table sx={{ width: "100%" }} size="small" aria-label="a dense table">
      <TableBody>
        {data.map((item, index) => (
          <TableRow
            key={index}
            sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
          >
            <TableCell component="th" scope="row" width={"20%"}>
              {item.header}
            </TableCell>
            <TableCell component="th" scope="row">
              {item.link ? (
                <Link
                  to={item.link}
                  target="_blank"
                  rel="noreferrer"
                  style={{ textDecoration: "none" }}
                >
                  {item.body}
                </Link>
              ) : (
                item.body
              )}
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
};

export default VersionDetails;
