import {
  Box,
  FormControl,
  FormLabel,
  Grid,
  List,
  ListItem,
  ListSubheader,
  Switch,
} from "@mui/material";
import React from "react";
import MetaFilters from "../MetaFilters";

const FilterSidebar = ({
  showFilters,
  handlePrimaryTextToggle,
  showPrimaryTextOnly,
  annotationFilter,
  setAnnotationFilter,
}) => {
  return (
    <Grid item md={2} xs={12}>
      <Box
        sx={{
          display: showFilters ? "block" : "none",
          pr: "10px",
        }}
      >
        <List subheader={<ListSubheader>Filters</ListSubheader>}>
          <ListItem>
            <FormControl
              component="fieldset"
              variant="standard"
              my={0}
              fullWidth
            >
              <FormLabel
                sx={{
                  py: "10px",
                  fontWeight: "600",
                  color: "rgba(0, 0, 0, 0.6) !important",
                }}
              >
                Primary Text
              </FormLabel>
              <Box display={"flex"} flexDirection={"column"} gap={1} mx={2}>
                <Box
                  display={"flex"}
                  alignItems={"center"}
                  justifyContent={"space-between"}
                >
                  <FormLabel
                    sx={{
                      color: "rgba(0, 0, 0, 0.6) !important",
                    }}
                  >
                    Primary Text Only
                  </FormLabel>
                  <Switch
                    size="small"
                    onChange={() => handlePrimaryTextToggle()}
                    checked={showPrimaryTextOnly}
                    color="pri"
                  />
                </Box>
              </Box>
            </FormControl>
          </ListItem>
          <ListItem>
            <MetaFilters
              annotationFilter={annotationFilter}
              setAnnotationFilter={setAnnotationFilter}
            />
          </ListItem>
        </List>
      </Box>
    </Grid>
  );
};

export default FilterSidebar;
