import React from "react";
import { Table, TableBody, TableCell, TableRow } from "@mui/material";
import { Link } from "react-router-dom";

const DownloadSrt = ({ version }) => {
  const data = [
    {
      header: `Download SRT ${version.version_uri
        .split(".")
        .slice(-1)
        .toString()} :`,
      body: `Download SRT Files (October2022) - ${version.version_id}`,
      link: `http://dev.kitab-project.org/passim01102022/${version.version_uri
        .split(".")
        .slice(-1)
        .toString()}`,
    },
  ];

  return (
    <Table sx={{ width: "100%" }} size="small" aria-label="a dense table">
      <TableBody>
        {data.map((item, index) => (
          <TableRow
            key={index}
            sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
          >
            <TableCell
              component="th"
              scope="row"
              width={"35%"}
              sx={{ textAlign: "left" }}
            >
              {item.header}
            </TableCell>
            <TableCell component="th" scope="row">
              {item.link ? (
                <Link
                  to={item.link}
                  target="_blank"
                  rel="noreferrer"
                  style={{ textDecoration: "none" }}
                >
                  {item.body}
                </Link>
              ) : (
                item.body
              )}
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
};

export default DownloadSrt;
