import React from "react";
import { displayNamesText } from "../../ColumnDisplayName";
import { Table, TableBody, TableCell, TableRow } from "@mui/material";
import { Link } from "react-router-dom";

const TextDetails = ({ text }) => {
  const data = [
    {
      header: Object.entries(displayNamesText)[0][1],
      body: text.id,
    },
    {
      header: Object.entries(displayNamesText)[1][1],
      body: text.text_uri,
    },
    {
      header: Object.entries(displayNamesText)[2][1],
      body: text.title_ar.split("::"),
    },
    {
      header: Object.entries(displayNamesText)[3][1],
      body: text.title_lat.split("::"),
    },
    {
      header: Object.entries(displayNamesText)[4][1],
      body: text.text_type,
    },
    {
      header: Object.entries(displayNamesText)[5][1],
      body: text.tags.split("::").join(";"),
    },
    {
      header: Object.entries(displayNamesText)[6][1],
      body: text.related_texts,
    },
    {
      header: Object.entries(displayNamesText)[7][1],
      body: text.related_persons,
    },
    {
      header: Object.entries(displayNamesText)[8][1],
      body: text.related_places,
    },
  ];

  return (
    <Table sx={{ width: "100%" }} size="small" aria-label="a dense table">
      <TableBody>
        {data.map((item, index) => (
          <TableRow
            key={index}
            sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
          >
            <TableCell component="th" scope="row" width={"20%"}>
              {item.header}
            </TableCell>
            <TableCell component="th" scope="row">
              {item.link ? (
                <Link
                  to={item.link}
                  target="_blank"
                  rel="noreferrer"
                  style={{ textDecoration: "none" }}
                >
                  {item.body}
                </Link>
              ) : (
                item.body
              )}
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
};

export default TextDetails;
