import { useState } from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import Menu from "@mui/material/Menu";
import MenuIcon from "@mui/icons-material/Menu";
import Link from "@mui/material/Link";
import MenuItem from "@mui/material/MenuItem";
import Avatar from "@mui/material/Avatar";
import { makeStyles } from "@mui/styles";
import Fade from "@mui/material/Fade";

const pages = [
  {
    label: "About",
    link: "http://kitab-project.org/about/",
  },
  {
    label: "Corpus and Data",
    link: "http://kitab-project.org/corpus-and-data",
    dropdown: [
      {
        label: "Metadata",
        link: "/metadata",
      },
      {
        label: "Insight",
        link: "/insight",
      },
    ],
  },
  {
    label: "Methods",
    link: "http://kitab-project.org/methods",
  },
  {
    label: "Research",
    link: "http://kitab-project.org/blogs",
  },
  {
    label: "Arabic Pasts",
    link: "http://kitab-project.org/research/events/arabic-pasts",
  },
  {
    label: "Text Reuse App",
    link: "/text-reuse-app",
  },
  {
    label: "Visualisation",
    link: "/visualise",
  },
];

const useStyles = makeStyles((theme) => ({
  responsiveBrand: {
    [theme.breakpoints.down("sm")]: {
      display: "none",
    },
    [theme.breakpoints.up("sm")]: {
      padding: "block",
    },
  },
}));

const Navigationbar = () => {
  const classes = useStyles();
  const [anchorElNav, setAnchorElNav] = useState(null);

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <AppBar
      position="static"
      elevation={1}
      sx={{
        display: "flex",
        alignItems: "center",
        bgcolor: "#eee",
        borderBottom: "1px solid #cecfd1",
        boxShadow: "none",
      }}
    >
      <Box
        sx={{
          width: "1350px",
          m: "auto",
          py: "1rem",
        }}
      >
        <Toolbar
          disableGutters
          sx={{
            display: "flex",
            justifyContent: "space-between",
          }}
        >
          <Link
            href="/"
            sx={{
              width: "100px",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
            className={classes.responsiveBrand}
          >
            <Avatar
              alt="KITAB Logo"
              src="https://kitab-project.org/assets/images/logo-small-2.png"
              sx={{
                height: "44px",
                width: "44px",
                borderRadius: "0px",
              }}
            />
          </Link>

          <Box sx={{ display: { xs: "flex", md: "none" } }}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="primary"
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "left",
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "left",
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: "block", md: "none" },
              }}
            >
              {pages.map((page) => (
                <MenuItem key={page.label} onClick={handleCloseNavMenu}>
                  <Typography textAlign="center">{page.label}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
          <Typography
            variant="h6"
            noWrap
            component="div"
            sx={{ flexGrow: 1, display: { xs: "flex", md: "none" } }}
          >
            <Avatar
              alt="KITAB Logo"
              src="https://kitab-project.org/assets/images/logo-small-2.png"
            />
          </Typography>
          <Box sx={{ display: { xs: "none", md: "flex" }, flexWrap: "wrap" }}>
            {pages.map((page) => (
              <Link
                key={page.label}
                href={page.dropdown ? null : page.link}
                sx={{
                  display: "block",
                  mx: "1.5rem",
                  pointer: page.dropdown ? "" : "none",
                  textDecoration: "none",
                  fontFamily: `-apple-system,BlinkMacSystemFont,"Roboto","Segoe UI","Helvetica Neue","Lucida Grande",Arial,sans-serif`,
                  fontSize: "18px",
                  color: "#222831",
                  position: "relative",
                }}
              >
                {!page.dropdown ? (
                  page.label
                ) : (
                  <div>
                    <div
                      id="fade-button"
                      aria-controls={open ? "fade-menu" : undefined}
                      aria-haspopup="true"
                      aria-expanded={open ? "true" : undefined}
                      onMouseOver={page.dropdown ? handleClick : null}
                    >
                      {page.label}
                      <i
                        className="fa-solid fa-caret-down"
                        style={{ marginLeft: "5px" }}
                      ></i>
                    </div>
                    <Menu
                      id="fade-menu"
                      MenuListProps={{
                        "aria-labelledby": "fade-button",
                      }}
                      anchorEl={anchorEl}
                      open={open}
                      TransitionComponent={Fade}
                      onClick={handleClose}
                    >
                      {page?.dropdown?.map((item, i) => (
                        <Link
                          sx={{
                            width: "220px",
                            height: "30px",
                            fontSize: "16px",
                            px: "10px",
                            textDecoration: "none",
                            display: "flex",
                            flexWrap: "wrap",
                            alignItems: "center",
                            color: "inherit",
                          }}
                          key={i}
                          href={item.link}
                        >
                          {item.label}
                        </Link>
                      ))}
                    </Menu>
                  </div>
                )}
              </Link>
            ))}
          </Box>
        </Toolbar>
      </Box>
    </AppBar>
  );
};
export default Navigationbar;
