import { getVersionMetadataById } from "../services/CorpusMetaData";

export const getBooks = async (book_names) => {
  var bn1 = book_names[0].match(/(\w+)-[a-z]{3}\d/)[1];
  var bn2 = book_names[1].match(/(\w+)-[a-z]{3}\d/)[1];
  bn1 = bn1.replace(/(\.completed)|(\.inProgress)|(\.mARkdown)/, "");
  bn2 = bn2.replace(/(\.completed)|(\.inProgress)|(\.mARkdown)/, "");
  bn2 = bn2.replace(/(\.csv)|(\.srt)|(\.completed)/, "");

  const b1 = await getVersionMetadataById(bn1);
  const b2 = await getVersionMetadataById(bn2);
  return {
    book1: b1?.results[0],
    book2: b2?.results[0],
  };
};
