import { Box, Input, InputLabel, Typography } from "@mui/material";

const UploadInput = ({ item, fileState, handleUpload }) => {
  return (
    <Box width={"100%"}>
      <InputLabel
        htmlFor={item.title}
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          cursor: "pointer",
          gap: "10px",
          height: "60px",
          px: "25px",
          bgcolor: "#F0F0F5",
          borderRadius: "5px",
        }}
      >
        <Box display={"flex"} alignItems={"center"} gap={"10px"}>
          {item.icon !== "" && (
            <img src={item.icon} alt={item.title} style={{ width: "30px" }} />
          )}
          <Typography textTransform={"none"} color={"black"}>
            {item.title}
          </Typography>
        </Box>
        <Typography textTransform={"none"} color={"black"}>
          {fileState}
        </Typography>
      </InputLabel>
      {item.title === "Upload SRT/CSV File" && (
        <Input
          type="file"
          id={item.title}
          sx={{ display: "none" }}
          onChange={(e) => handleUpload(e.target.files[0])}
        />
      )}
    </Box>
  );
};

export default UploadInput;
