import { Box } from "@mui/material";
import React, { useEffect, useState } from "react";
import { getGHRepo } from "../../../../utility/Helper";
import { displayNamesAuthor } from "../../ColumnDisplayName";
import RelatedPersonsTable from "./RelatedPersonsTable";
import { Link } from "react-router-dom";
import { getAuthorByUri } from "../../../../services/CorpusMetaData";

const AuthorDetails = ({ author, versionDetail }) => {
  const [authorData, setAuthorData] = useState([]);
  const data = [
    {
      header: Object.entries(displayNamesAuthor)[0][1],
      body: author.id,
    },
    {
      header: Object.entries(displayNamesAuthor)[1][1],
      body: author.author_uri,
      link: getGHRepo(author.date) + author.author_uri,
    },
    {
      header: Object.entries(displayNamesAuthor)[2][1],
      body: author.author_lat.split("::"),
    },
    {
      header: Object.entries(displayNamesAuthor)[3][1],
      body: author.author_ar.split("::"),
    },
    {
      header: Object.entries(displayNamesAuthor)[4][1],
      body: author.date,
    },
    {
      header: Object.entries(displayNamesAuthor)[5][1],
      body: author.authorDateAH,
    },
    {
      header: Object.entries(displayNamesAuthor)[6][1],
      body: author.authorDateCE,
    },
    {
      header: Object.entries(displayNamesAuthor)[7][1],
      body: author.authorDateString,
    },
    {
      header: Object.entries(displayNamesAuthor)[8][1],
      body: versionDetail.text_id["related_text"],
    },
    {
      header: Object.entries(displayNamesAuthor)[9][1],
      body: author.related_places,
    },
  ];

  useEffect(() => {
    const fetchData = async () => {
      const data = await getAuthorByUri(author.author_uri);
      setAuthorData(data.results);
    };
    fetchData();
  }, [author]);

  return (
    <Box sx={{ width: "100%" }} size="small" aria-label="a dense table">
      {data.map((item, index) => (
        <Box
          key={index}
          sx={{
            width: "100%",
            display: "flex",
            margin: "5px 0px",
          }}
        >
          {item.header === "Related Persons" ? (
            <Box width={"100%"} margin={"5px 0px"}>
              {item.header}
              <RelatedPersonsTable data={authorData} />
            </Box>
          ) : (
            <Box sx={{ width: "25%" }}>{item.header}</Box>
          )}

          {item.header === "Related Persons" ? (
            ""
          ) : (
            <Box
              sx={{
                width: "75%",
              }}
            >
              {item.link ? (
                <Link
                  to={item.link}
                  target="_blank"
                  rel="noreferrer"
                  style={{ textDecoration: "none" }}
                >
                  {item.body}
                </Link>
              ) : (
                item.body
              )}
            </Box>
          )}
        </Box>
      ))}
    </Box>
  );
};

export default AuthorDetails;
