import AccountTreeIcon from "@mui/icons-material/AccountTree";
import CloseIcon from "@mui/icons-material/Close";
import { AppBar, IconButton, Stack } from "@mui/material";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import Tab from "@mui/material/Tab";
import Tabs from "@mui/material/Tabs";
import PropTypes from "prop-types";
import { useContext, useState } from "react";
import { DrawerContext, DrawerDataContext } from "../MetadataTable";
import GetAppOutlinedIcon from "@mui/icons-material/GetAppOutlined";
import Tooltip from "@mui/material/Tooltip";
import { deleteKey } from "../../../utility/Helper";
import TextDetails from "./TextDetails";
import AuthorDetails from "./AuthorDetails";
import VersionDetails from "./VersionDetails";
import DownloadSrt from "./DownloadSrt";

export default function LeftSidePanel() {
  const { versionDetail } = useContext(DrawerDataContext);
  const { close, isOpen } = useContext(DrawerContext);
  const [value, setValue] = useState(0);

  const handleDownloadJsonClick = (row) => {
    const link = document.createElement("a");
    link.href = `data:text/json;charset=utf-8,${encodeURIComponent(
      JSON.stringify(versionDetail)
    )}`;
    link.download = `version_details.json`;
    const clickEvt = new MouseEvent("click", {
      view: window,
      bubbles: true,
      cancelable: true,
    });
    link.dispatchEvent(clickEvt);
    link.remove();
  };

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  function a11yProps(index) {
    return {
      id: `simple-tab-${index}`,
      "aria-controls": `simple-tabpanel-${index}`,
    };
  }
  function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
        aria-labelledby={`simple-tab-${index}`}
        {...other}
      >
        {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
      </div>
    );
  }

  TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
  };

  console.log(versionDetail);

  const version = deleteKey("text_id", { ...versionDetail });
  const text = deleteKey("author_id", { ...versionDetail.text_id });
  const author = deleteKey("text_id", {
    ...((versionDetail || {}).text_id || {}).author_id,
  });

  return (
    <>
      <Drawer open={isOpen} onClose={close} anchor="right">
        <Box
          sx={{
            width: {
              xl: "40vw",
              md: "50vw",
              sm: "100vw",
            },
          }}
        >
          <AppBar position="sticky" color={"neutral"} heigth={"1vw"}>
            <Stack
              direction="row"
              justifyContent={"space-between"}
              p={1}
              color="notYetAnnotated"
            >
              <Stack spacing={2} direction={"row"}>
                <Tooltip title={"Download this record in Json"} arrow>
                  <IconButton
                    onClick={handleDownloadJsonClick}
                    color={"iconColor"}
                    aria-label="Download this record in Json"
                  >
                    <GetAppOutlinedIcon />
                  </IconButton>
                </Tooltip>

                <Tooltip title={"Download this record in Json"} arrow>
                  <IconButton
                    onClick={handleDownloadJsonClick}
                    color={"iconColor"}
                    aria-label="Download this record in Json"
                  >
                    <AccountTreeIcon />
                  </IconButton>
                </Tooltip>
              </Stack>
              <Stack direction={"row"}>
                <Tooltip title={"Close"} arrow>
                  <IconButton
                    onClick={close}
                    color={"iconColor"}
                    aria-label="Close"
                  >
                    <CloseIcon />
                  </IconButton>
                </Tooltip>
              </Stack>
            </Stack>
          </AppBar>

          <Box sx={{ width: "100%" }}>
            <Box
              sx={{
                borderBottom: 1,
                borderColor: "divider",
              }}
            >
              <Tabs
                value={value}
                onChange={handleChange}
                aria-label="Full Record Details"
                variant="scrollable"
                scrollButtons="auto"
              >
                <Tab
                  label="Author Details"
                  {...a11yProps(0)}
                  sx={{ padding: "0px 10px", fontSize: "12px" }}
                />
                <Tab
                  label="Text Details"
                  {...a11yProps(1)}
                  sx={{ padding: "0px 10px", fontSize: "12px" }}
                />
                <Tab
                  label="Version Details"
                  {...a11yProps(2)}
                  sx={{ padding: "0px 10px", fontSize: "12px" }}
                />
                <Tab
                  label="Download SRT/CSV File (passim)"
                  {...a11yProps(4)}
                  sx={{ padding: "0px 10px", fontSize: "12px" }}
                />
              </Tabs>
            </Box>
            <TabPanel value={value} index={0}>
              <AuthorDetails author={author} versionDetail={versionDetail} />
            </TabPanel>
            <TabPanel value={value} index={1}>
              <TextDetails text={text} />
            </TabPanel>
            <TabPanel value={value} index={2}>
              <VersionDetails version={version} text={text} />
            </TabPanel>
            <TabPanel value={value} index={3}>
              <DownloadSrt version={version} />
            </TabPanel>
          </Box>
        </Box>
      </Drawer>
    </>
  );
}
