import { Box } from "@mui/material";
import React from "react";
import BookTitle from "./BookTitle";
import Author from "./Author";
import Version from "./Version";
import AuthorDeath from "./AuthorDeath";
import MoreAction from "./MoreAction";

const TableBody = ({ data, toggleSidePanel }) => {
  return (
    <Box>
      <Box
        sx={{
          p: 1,
          bgcolor: "#2863A5",
        }}
      ></Box>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          gap: 1,
          bgcolor: "#eee",
          p: 2,
        }}
      >
        <Version data={data} />
        <BookTitle data={data} />
        <Author data={data} />
        <AuthorDeath data={data} />
        <MoreAction data={data} toggleSidePanel={toggleSidePanel} />
      </Box>
    </Box>
  );
};

export default TableBody;
