import React from "react";
import YMLIcon from "../../../../../assets/img/yml.png";
import { Box, Typography } from "@mui/material";
import { getAuthorTwentyFiveIncrease } from "../../../../../functions/getAuthorTwentyFiveIncrease";
import FiberManualRecordIcon from "@mui/icons-material/FiberManualRecord";
import InfoOutlinedIcon from "@mui/icons-material/InfoOutlined";

const Version = ({ data }) => {
  let link = data?.version_uri.split(".")[0]
    ? `https://github.com/OpenITI/${
        getAuthorTwentyFiveIncrease(
          data?.text_id && data?.text_id.author_id.date
        ) < 100
          ? "00"
          : ""
      }${getAuthorTwentyFiveIncrease(
        data?.text_id && data?.text_id.author_id.date
      )}AH/blob/master/data/${
        data?.version_uri.split(".")[0]
      }/${data?.version_uri
        .split(".")
        .slice(0, 2)
        .join(".")}/${data?.version_uri.split(".").slice(0, 2).join(".")}.yml`
    : "";

  return (
    <Box
      display={"flex"}
      flexDirection={"column"}
      pb={2}
      borderBottom={"1px solid #cbd5e1"}
    >
      <Typography
        variant="body2"
        color={"text.primary"}
        textAlign={"right"}
        fontWeight={600}
      >
        Version ID
      </Typography>
      <Typography variant="body2" textAlign={"right"}>
        <a href={data?.url} target="_blank" rel="noreferrer" pr={1}>
          {data?.version_id}
        </a>

        <Typography
          variant="body2"
          direction={"row"}
          display={"flex"}
          alignItems={"center"}
          justifyContent={"flex-end"}
          mt={1}
        >
          <FiberManualRecordIcon fontSize="small" color={"invisible"} />
          <FiberManualRecordIcon
            fontSize="small"
            color={data?.annotation_status}
            aria-label="Annotation Status"
          />

          {data?.status === "pri" ? (
            <InfoOutlinedIcon
              fontSize="small"
              color={data?.status}
              title="Primary Text"
            />
          ) : (
            ""
          )}

          <a
            href={link}
            title={data?.author_uri}
            target="_blank"
            rel="noreferrer"
            style={{
              marginTop: "5px",
            }}
          >
            <img src={YMLIcon} className="img-icon" alt="yml" />
            {data?.author_uri}
          </a>
        </Typography>
      </Typography>
    </Box>
  );
};

export default Version;
