import { useCallback, useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";
import { Box, Grid, IconButton, TextField } from "@mui/material";
import FilterAltOutlinedIcon from "@mui/icons-material/FilterAltOutlined";
import CloseIcon from "@mui/icons-material/Close";
// import ResetIcon from "@mui/icons-material/Clear";

const SearchAndButtons = ({
  handleFilterPanel,
  handleResetFilters,
  getQuery,
}) => {
  const [searchParams, setSearchParams] = useSearchParams();
  const [text, setText] = useState("");

  const handleSearch = (e) => {
    e.preventDefault();
    const params = Object.fromEntries([...searchParams]);
    if (text) {
      setSearchParams({ ...params, search: text });
    } else {
      searchParams.delete("search");
      setSearchParams(searchParams);
    }
  };

  const getSearchQuery = useCallback(() => {
    const delaySearch = setTimeout(() => {
      if (searchParams.get("search")) {
        getQuery(searchParams.get("search"));
      } else {
        getQuery("");
      }
    }, 100);

    return () => clearTimeout(delaySearch);
  }, [getQuery, searchParams]);

  useEffect(() => {
    getSearchQuery();
  }, [getSearchQuery]);

  const updateText = useCallback(() => {
    if (searchParams.has("search")) {
      setText(searchParams.get("search"));
    } else {
      setText("");
    }
  }, [searchParams]);

  useEffect(() => {
    updateText();
  }, [updateText]);

  return (
    <Box display={"flex"} justifyContent={"space-between"} gap={2} my={2}>
      <Grid container item md={12} xs={12}>
        <Grid display={"flex"} item xs={12} md={1}>
          <IconButton onClick={handleFilterPanel}>
            <FilterAltOutlinedIcon />
          </IconButton>
          {searchParams.size ? (
            <IconButton onClick={handleResetFilters}>
              <CloseIcon />
            </IconButton>
          ) : (
            ""
          )}
        </Grid>
        <Grid display={"flex"} item xs={12} md={11}>
          <Grid sx={{ width: "100%" }} className="Search">
            <form onSubmit={(e) => handleSearch(e)}>
              <TextField
                id="outlined-search"
                label="Search by keyword or attributes"
                type="search"
                size="small"
                value={text}
                onChange={(e) => setText(e.target.value)}
                fullWidth={true}
              />
            </form>
          </Grid>
        </Grid>
      </Grid>
    </Box>
  );
};

export default SearchAndButtons;
