const DEV_BASE_URL = "http://127.0.0.1:8000";
const PROD_BASE_URL = "https://kitab-metadata-api.azurewebsites.net";
const DEV_ENV = false;

let RESOURCE = "/version/all/";

// filter data by search params
export async function getCorpusMetaData(
  page,
  pagesize,
  query,
  annotationFilter,
  orderBy,
  status
) {
  let QUERY_PARAMS = "";
  let annotationFilterQuery = annotationFilter
    ? `&annotation_status=${annotationFilter}`
    : "";
  let searchQuery = query ? `&search=${query}` : "";
  let statusQuery = status ? `&status=pri` : "";

  // if (query.length === 0) {
  //   QUERY_PARAMS = `?ordering=${orderBy}&page=${
  //     !page ? 1 : page
  //   }&page_size=${pagesize}`;
  // } else {
  //   QUERY_PARAMS = `?search=${query}`;
  // }

  // if (annotationFilter || status || query) {
  //   QUERY_PARAMS = `?ordering=${orderBy}&page=${
  //     !page ? 1 : page
  //   }&page_size=${pagesize}${annotationFilterQuery}${statusQuery}${searchQuery}`;
  // }

  QUERY_PARAMS = `?ordering=${orderBy}&page=${
    !page ? 1 : page
  }&page_size=${pagesize}${annotationFilterQuery}${statusQuery}${searchQuery}`;

  const res = await fetch(
    `${
      DEV_ENV
        ? DEV_BASE_URL + RESOURCE + QUERY_PARAMS
        : PROD_BASE_URL + RESOURCE + QUERY_PARAMS
    }`,
    { mode: "cors" }
  );
  const json = await res.json();
  return json;
}

// get author by uri
export async function getAuthorByUri(author_uri) {
  let QUERY_PARAMS = "";
  RESOURCE = "/author/all/";
  QUERY_PARAMS = "?author_uri=" + author_uri;

  const res = await fetch(
    `${
      DEV_ENV
        ? "DEV_BASE_URL" + RESOURCE + QUERY_PARAMS
        : PROD_BASE_URL + RESOURCE + QUERY_PARAMS
    }`,
    { mode: "cors" }
  );
  const json = await res.json();
  return json;
}

// get version meta data by id
// CHECK CONTAINS FILTER
export async function getVersionMetadataById(version_id) {
  let QUERY_PARAMS = "";
  RESOURCE = "/version/all/";
  QUERY_PARAMS = "?version_uri_contains=" + version_id;

  const res = await fetch(
    `${
      DEV_ENV
        ? "DEV_BASE_URL" + RESOURCE + QUERY_PARAMS
        : PROD_BASE_URL + RESOURCE + QUERY_PARAMS
    }`,
    { mode: "cors" }
  );
  const json = await res.json();
  return json;
}

export async function getAggregatedStats() {
  const res = await fetch(
    `${DEV_ENV ? DEV_BASE_URL : PROD_BASE_URL}/aggregatedstats/`,
    { mode: "cors" }
  );
  const data = await res.json();
  return data;
}
