import Layout from "../components/Layouts/Layout";
import { Box } from "@mui/material";
import Header from "../components/Visualisation/Header";
import UploadInput from "../components/Visualisation/UploadInput";
import OtherComponents from "../components/Visualisation/OtherComponents";
import BookMetadata from "../components/Visualisation/BookMetadata";
import Visual from "../components/Visualisation/Visual";
import React, { useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";
import Papa from "papaparse";
import { getBooks } from "../functions/getBooks";

const data = [
  {
    title: "Upload SRT/CSV File",
    icon: "./icons/upload.svg",
    isDropdown: false,
  },
  {
    title: "Books Metadata",
    icon: "./icons/file.svg",
    isDropdown: true,
  },
  {
    title: "Visualisation",
    icon: "./icons/chart.svg",
    isDropdown: true,
  },
  {
    title: "Books Alignment",
    icon: "./icons/align.svg",
    isDropdown: true,
  },
  {
    title: "Books",
    icon: "./icons/book.svg",
    isDropdown: true,
  },
];

const Visualisation = () => {
  const [bookOne, setBookOne] = useState();
  const [bookTwo, setBookTwo] = useState();

  const [fileState, setFileState] = useState("");
  const [searchParams, setSearchParams] = useSearchParams();
  const [chartData, setChartData] = useState([]);

  const handleUpload = async (value) => {
    setFileState(value?.name);
    setSearchParams({
      file: value?.name,
    });
    const book_names = value?.name.split("_");
    const book1 = (await getBooks(book_names)).book1;
    const book2 = (await getBooks(book_names)).book2;

    localStorage.setItem("first_token", JSON.stringify(book1?.tok_length));
    localStorage.setItem("second_token", JSON.stringify(book2?.tok_length));
    Papa.parse(value, {
      header: true,
      skipEmptyLines: true,
      complete: function (results) {
        localStorage.setItem("chartData", JSON.stringify(results));
        window.location.reload();
      },
    });
  };

  useEffect(() => {
    if (searchParams.get("file")) {
      setFileState(searchParams.get("file"));
      const fetchData = async () => {
        const book_names = searchParams.get("file")?.split("_");
        const book1 = (await getBooks(book_names)).book1;
        const book2 = (await getBooks(book_names)).book2;
        setBookTwo(book2);
        setBookOne(book1);
      };
      fetchData();
    }
  }, [searchParams]);

  const getToggledComponent = (title) => {
    if (title === "Books Metadata") {
      if (bookOne && bookTwo) {
        return <BookMetadata bookOne={bookOne} bookTwo={bookTwo} />;
      }
    } else if (title === "Visualisation") {
      if (localStorage.getItem("chartData")) {
        return <Visual data={chartData} />;
      }
    } else {
      return null;
    }
  };

  useEffect(() => {
    if (localStorage.getItem("chartData")) {
      setChartData(JSON.parse(localStorage.getItem("chartData")).data);
    }
  }, []);

  return (
    <Layout>
      <Box
        width={"1280px"}
        mx={"auto"}
        py={"50px"}
        display={"flex"}
        flexDirection={"column"}
        gap={3}
      >
        <Header />
        {data.map((item, i) => (
          <Box key={i}>
            {item.title === "Upload SRT/CSV File" ? (
              <UploadInput
                item={item}
                fileState={fileState}
                handleUpload={handleUpload}
              />
            ) : (
              <OtherComponents item={item} />
            )}
            {getToggledComponent(item.title)}
          </Box>
        ))}
      </Box>
    </Layout>
  );
};

export default Visualisation;
