import { Box, TableCell, TableHead, TableRow } from "@mui/material";
import React from "react";
import AuthorDateSort from "../SortingButtons/AuthorDateSort";

const TableHeader = ({
  columns,
  classes,
  sortingOrder,
  handleSortingFilter,
}) => {
  const getWidth = (value) => {
    if (value === "More... ") {
      return "5%";
    } else if (value === "Author Death Date ") {
      return "15%";
    } else if (value === "Book Length") {
      return "10%";
    } else if (value === "Book Title") {
      return "30%";
    } else if (value === "Author") {
      return "25%";
    } else if (value === "Version ID") {
      return "15%";
    } else {
      return "auto";
    }
  };

  return (
    <TableHead
      sx={{
        color: "text.primary",
        fontSize: 34,
        fontWeight: "medium",
      }}
    >
      <TableRow
        sx={{
          width: "100%",
          display: "flex",
          alignItems: "center",
        }}
      >
        {columns.map((column) => (
          <TableCell
            className={classes.tableHeaderCell}
            key={column.field}
            sx={{
              width: `${getWidth(column.headerName)} !important`,
              height: "30px",
              display: "flex",
              alignItems: "center",
            }}
          >
            <Box display={"flex"} alignItems={"center"}>
              {column.headerName === "Author Death Date " ? (
                <AuthorDateSort
                  sortingOrder={sortingOrder}
                  handleSortingFilter={handleSortingFilter}
                />
              ) : (
                " "
              )}
              {column.headerName}
            </Box>
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
};

export default TableHeader;
