import { Box, Typography } from "@mui/material";
import React from "react";

const OtherComponents = ({ item }) => {
  return (
    <Box
      display={"flex"}
      justifyContent={"space-between"}
      gap={"0px"}
      sx={{
        display: "flex",
        alignItems: "center",
        height: "60px",
        px: "25px",
        gap: "10px",
        bgcolor: "#F0F0F5",
        borderRadius: "5px",
      }}
    >
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          gap: "10px",
        }}
      >
        {item.icon !== "" && (
          <img src={item.icon} alt={item.title} style={{ width: "30px" }} />
        )}
        <Typography textTransform={"none"} color={"black"}>
          {item.title}
        </Typography>
      </Box>
      <img
        src="./icons/down-arrow.svg"
        alt="down-arrow"
        style={{ width: "20px" }}
      />
    </Box>
  );
};

export default OtherComponents;
