import React from "react";
import { Box, Grid, Paper, Typography } from "@mui/material";
import SelectDropdown from "./SelectDropdown";

const data = [
  {
    label: "Data One",
    value: 1,
  },
  {
    label: "Data Two",
    value: 2,
  },
  {
    label: "Data Three",
    value: 3,
  },
  {
    label: "Data Four",
    value: 4,
  },
];

const FilterComponent = () => {
  return (
    <Box sx={{ px: "100px", py: "50px" }}>
      <Box component={Paper}>
        <Box
          sx={{
            bgcolor: "#eee",
            p: "10px",
          }}
        >
          <Typography variant="body1">
            Find a book to discover alignments
          </Typography>
        </Box>
        <Grid container flexWrap={"nowrap"} gap={3} p={3}>
          <SelectDropdown label={"Available Aligned Books"} data={data} />
          <SelectDropdown label={"Select Book"} data={data} />
          <SelectDropdown label={"Select Genre"} data={data} />
          <SelectDropdown label={"Select Century"} data={data} />
        </Grid>
      </Box>
    </Box>
  );
};

export default FilterComponent;
