import { ButtonGroup, IconButton, TableCell } from "@mui/material";
import React from "react";
import DownLoadDropdown from "../../../DownLoadDropdown";
import MoreHorizIcon from "@mui/icons-material/MoreHoriz";

const MoreCell = ({ classes, row, toggleSidePanel }) => {
  return (
    <TableCell
      className={classes.tableCell}
      sx={{
        width: "5%",
        border: "none",
      }}
    >
      <ButtonGroup variant="outlined" aria-label="outlined button group">
        <IconButton
          onClick={() => {
            toggleSidePanel(row);
          }}
          size={"small"}
          color={"neutral"}
        >
          <MoreHorizIcon />
        </IconButton>
        <DownLoadDropdown versionURI={row?.version_uri} />
      </ButtonGroup>
    </TableCell>
  );
};

export default MoreCell;
