import { Stack, TableCell, Typography } from "@mui/material";
import YMLIcon from "../../../../../assets/img/yml.png";
import { getGitHubRepoURL } from "../../../../../utility/Helper";
import React from "react";
import { getAuthorTwentyFiveIncrease } from "../../../../../functions/getAuthorTwentyFiveIncrease";

const AuthorCell = ({ row, classes }) => {
  let link = row?.version_uri.split(".")[0]
    ? `https://github.com/OpenITI/${
        getAuthorTwentyFiveIncrease(
          row?.text_id && row?.text_id.author_id.date
        ) < 100
          ? "00"
          : ""
      }${getAuthorTwentyFiveIncrease(
        row?.text_id && row?.text_id.author_id.date
      )}AH/blob/master/data/${row?.version_uri.split(".")[0]}/${
        row?.version_uri.split(".")[0]
      }.yml`
    : "";

  return (
    <TableCell
      className={classes.tableCell}
      sx={{
        width: "25%",
        border: "none",
      }}
    >
      <Stack spacing={0}>
        <Typography
          variant="body2"
          my={0}
          display={"flex"}
          alignItems={"center"}
          gap={1}
        >
          <a
            href={getGitHubRepoURL(row.version_uri, "a")}
            target="_blank"
            rel="noreferrer"
          >
            {row?.text_id &&
              row?.text_id.author_id.author_lat.split(" :: ")[0].toUpperCase()}
          </a>
          <a href={link} target="_blank" rel="noreferrer">
            <img src={YMLIcon} className="img-icon" alt="yml" />
          </a>
          {row?.author_uri}
        </Typography>
        <Typography variant="body2" my={0}>
          {row?.text_id && row?.text_id.author_id.author_ar.split(" :: ")[0]}
        </Typography>
      </Stack>
    </TableCell>
  );
};

export default AuthorCell;
