import { Box, Link, Typography } from "@mui/material";

const BookMetadata = ({ bookOne, bookTwo }) => {
  return (
    <Box py={2} px={5}>
      <Box display={"flex"}>
        <Box display={"flex"} flexDirection={"column"} gap={1} width={"50%"}>
          <Typography variant="body2" fontWeight={600}>
            Book Title :{" "}
            <Link
              sx={{ textDecoration: "none" }}
              href={
                bookOne
                  ? `http://localhost:8080/?search=${bookOne?.version_id}`
                  : "/"
              }
            >
              {bookOne
                ? bookOne?.text_id.text_uri
                    .split(".")[1]
                    .replace(/([A-Z])/g, " $1")
                    .trim()
                : "N/A"}
            </Link>
            <Link
              sx={{ ml: "10px", color: "grey" }}
              href={
                bookOne
                  ? `http://localhost:8080/?search=${bookOne?.version_id}`
                  : "/"
              }
            >
              <i className="fa-solid fa-up-right-from-square"></i>
            </Link>
          </Typography>
          <Typography variant="body2" fontWeight={600}>
            Book Author :{" "}
            {bookTwo ? bookOne?.text_id.author_id.author_lat : "N/A"}
          </Typography>
          <Typography variant="body2" fontWeight={600}>
            Book Id : {bookOne ? bookOne?.version_id : "N/A"}
          </Typography>
          <Typography variant="body2" fontWeight={600}>
            Word Count : {bookOne ? bookOne?.tok_length : "N/A"}
          </Typography>
        </Box>
        <Box display={"flex"} flexDirection={"column"} gap={1} width={"50%"}>
          <Typography variant="body2" fontWeight={600}>
            Book Title :{" "}
            <Link
              sx={{ textDecoration: "none" }}
              href={
                bookTwo
                  ? `http://localhost:8080/?search=${bookTwo?.version_id}`
                  : "/"
              }
            >
              {bookTwo
                ? bookTwo?.text_id.text_uri
                    .split(".")[1]
                    .replace(/([A-Z])/g, " $1")
                    .trim()
                : "N/A"}
            </Link>
            <Link
              sx={{ ml: "10px", color: "grey" }}
              href={
                bookTwo
                  ? `http://localhost:8080/?search=${bookTwo?.version_id}`
                  : "/"
              }
            >
              <i className="fa-solid fa-up-right-from-square"></i>
            </Link>
          </Typography>
          <Typography variant="body2" fontWeight={600}>
            Book Author :{" "}
            {bookTwo ? bookTwo?.text_id.author_id.author_lat : "N/A"}
          </Typography>
          <Typography variant="body2" fontWeight={600}>
            Book Id : {bookTwo ? bookTwo?.version_id : "N/A"}
          </Typography>
          <Typography variant="body2" fontWeight={600}>
            Word Count : {bookTwo ? bookTwo?.tok_length : "N/A"}
          </Typography>
        </Box>
      </Box>
    </Box>
  );
};

export default BookMetadata;
