import { Divider, Stack, TableCell } from "@mui/material";
import FiberManualRecordIcon from "@mui/icons-material/FiberManualRecord";
import InfoOutlinedIcon from "@mui/icons-material/InfoOutlined";
import YMLIcon from "../../../../../assets/img/yml.png";
import React from "react";
import { getAuthorTwentyFiveIncrease } from "../../../../../functions/getAuthorTwentyFiveIncrease";
import { Link } from "react-router-dom";

const VersionIdCell = ({ row, classes }) => {
  let link = row?.version_uri.split(".")[0]
    ? `https://github.com/OpenITI/${
        getAuthorTwentyFiveIncrease(
          row?.text_id && row?.text_id.author_id.date
        ) < 100
          ? "00"
          : ""
      }${getAuthorTwentyFiveIncrease(
        row?.text_id && row?.text_id.author_id.date
      )}AH/blob/master/data/${row?.version_uri.split(".")[0]}/${row?.version_uri
        .split(".")
        .slice(0, 2)
        .join(".")}/${row?.version_uri}.yml`
    : "";

  return (
    <TableCell
      className={classes.tableCell}
      sx={{
        width: "15%",
        border: "none",
      }}
    >
      <Stack spacing={2}>
        <Link to={row?.url} target="_blank" rel="noreferrer" pr={1}>
          {row?.version_id}
        </Link>

        <Stack
          direction={"row"}
          spacing={1}
          divider={<Divider orientation="vertical" flexItem />}
        >
          <FiberManualRecordIcon fontSize="x-small" color={"invisible"} />
          <FiberManualRecordIcon
            fontSize="small"
            color={row?.annotation_status}
            aria-label="Annotation Status"
          />

          {row?.status === "pri" ? (
            <InfoOutlinedIcon
              fontSize="small"
              color={row?.status}
              title="Primary Text"
            />
          ) : (
            ""
          )}

          <Link
            to={link}
            title={row?.author_uri}
            target="_blank"
            rel="noreferrer"
          >
            <img src={YMLIcon} className="img-icon" alt="yml" />
            {row?.author_uri}
          </Link>
        </Stack>
      </Stack>
    </TableCell>
  );
};

export default VersionIdCell;
