import React from "react";
import { Box, Typography } from "@mui/material";

const AuthorDeath = ({ data }) => {
  return (
    <Box
      display={"flex"}
      flexDirection={"column"}
      pb={2}
      borderBottom={"1px solid #cbd5e1"}
    >
      <Typography
        variant="body2"
        color={"text.primary"}
        textAlign={"right"}
        fontWeight={600}
      >
        Author's Death
      </Typography>
      <Typography variant="body2" color={"text.primary"} textAlign={"right"}>
        {data.text_id && data.text_id.author_id.date}
      </Typography>
    </Box>
  );
};

export default AuthorDeath;
