import React from "react";
import Paper from "@mui/material/Paper";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableRow from "@mui/material/TableRow";
import TableHeader from "./TableHeader";
import BookTitleCell from "./TableBody/BookTitleCell";
import AuthorCell from "./TableBody/AuthorCell";
import VersionIdCell from "./TableBody/VersionIdCell";
import MoreCell from "./TableBody/MoreCell";

const columns = [
  { field: "book_id", headerName: "Version ID", minWidth: 200 },
  { field: "title_lat", headerName: "Book Title", minWidth: 100 },
  { field: "author_lat", headerName: "Author", minWidth: 100 },
  {
    field: "tok_length",
    headerName: "Book Length",
    minWidth: 100,
    type: "number",
  },
  { field: "date", headerName: "Author Death Date ", minWidth: 100 },
  { field: "", headerName: "More... ", minWidth: 100 },
];

const TableComponent = ({
  handleSortingFilter,
  toggleSidePanel,
  rows,
  classes,
  sortingOrder,
}) => {
  return (
    <TableContainer
      component={Paper}
      dir="rtl"
      sx={{
        display: {
          xs: "none",
          md: "block",
        },
      }}
    >
      <Table
        sx={{ display: "flex", flexDirection: "column" }}
        size="small"
        stickyHeader
      >
        <TableHeader
          columns={columns}
          classes={classes}
          sortingOrder={sortingOrder}
          handleSortingFilter={handleSortingFilter}
        />
        <TableBody>
          {rows.map((row, index) => (
            <TableRow
              key={index}
              className={classes.tableCell}
              sx={{
                display: "flex",
                width: "100%",
                alignItems: "center",
                borderWidth: "0px",
                borderColor: "#d1d5db",
                borderStyle: "solid",
                borderBottomWidth: "1px",
                "&:last-child": {
                  borderBottomWidth: "0px",
                },
              }}
            >
              <VersionIdCell row={row} classes={classes} />

              <BookTitleCell row={row} classes={classes} />

              <AuthorCell row={row} classes={classes} />

              <TableCell
                className={classes.tableCell}
                sx={{ width: "10%", border: "none" }}
              >
                {row.tok_length}
              </TableCell>
              <TableCell
                className={classes.tableCell}
                sx={{ width: "15%", border: "none" }}
              >
                {row.text_id && row.text_id.author_id.date}
              </TableCell>

              <MoreCell
                classes={classes}
                row={row}
                toggleSidePanel={toggleSidePanel}
              />
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default TableComponent;
