import { useEffect } from "react";
import FormLabel from "@mui/material/FormLabel";
import FormControl from "@mui/material/FormControl";
import Switch from "@mui/material/Switch";
import { useSearchParams } from "react-router-dom";
import { Box } from "@mui/material";

function MetaFilters({ annotationFilter, setAnnotationFilter }) {
  const [searchParams, setSearchParams] = useSearchParams();

  const data = [
    {
      label: "Pending",
      value: annotationFilter.notYetAnnotated,
      name: "notYetAnnotated",
    },
    {
      label: "In Progress",
      value: annotationFilter.inProgress,
      name: "inProgress",
    },
    {
      label: "Completed",
      value: annotationFilter.completed,
      name: "completed",
    },
    {
      label: "Completed and Vetted",
      value: annotationFilter.mARkdown,
      name: "mARkdown",
    },
  ];

  const filterString = (values) => {
    let filterValues = "";
    let keys = Object.keys(values);
    keys.forEach((key) => {
      if (values[key]) {
        filterValues += key ? key + "," : key;
      }
    });
    filterValues = filterValues.replace("q,", "");
    return filterValues;
  };

  const handleChange = (event) => {
    setAnnotationFilter({
      ...annotationFilter,
      [event.target.name]: event.target.checked,
      q: filterString({
        ...annotationFilter,
        [event.target.name]: event.target.checked,
      }),
    });
    const filters = filterString({
      ...annotationFilter,
      [event.target.name]: event.target.checked,
    });
    if (filters) {
      const params = Object.fromEntries([...searchParams]);
      setSearchParams({ ...params, annotation_status: filters });
    } else {
      searchParams.delete("annotation_status");
      setSearchParams(searchParams);
    }
  };

  useEffect(() => {
    if (searchParams.get("annotation_status")) {
      const annotationStatusArr = searchParams
        .get("annotation_status")
        .split(",");

      const updatedFilter = annotationStatusArr.reduce((acc, item) => {
        if (annotationFilter[item] !== undefined) {
          acc[item] = true;
        }
        return acc;
      }, {});

      setAnnotationFilter({
        ...updatedFilter,
        q: searchParams.get("annotation_status"),
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      <FormControl my={0} fullWidth>
        <FormLabel
          sx={{
            py: "10px",
            fontWeight: "600",
            color: "rgba(0, 0, 0, 0.6) !important",
          }}
        >
          Annotation Status:
        </FormLabel>
        <Box display={"flex"} flexDirection={"column"} gap={1} mx={2}>
          {data.map((item, i) => (
            <Box
              key={i}
              display={"flex"}
              alignItems={"center"}
              justifyContent={"space-between"}
            >
              <FormLabel
                sx={{
                  color: "rgba(0, 0, 0, 0.6) !important",
                }}
              >
                {item.label}
              </FormLabel>
              <Switch
                size="small"
                checked={item.value}
                onChange={handleChange}
                name={item.name}
                color={item.name}
              />
            </Box>
          ))}
        </Box>
      </FormControl>
    </>
  );
}

export default MetaFilters;
