import { Box } from "@mui/material";
import React, { useEffect, useRef } from "react";
import * as d3 from "d3";

const Visual = ({ data }) => {
  const chartContainer = useRef();
  const svgChart = useRef();

  useEffect(() => {
    let margin = {
      top: 40,
      left: 30,
    };

    let width = chartContainer.current.clientWidth;
    const height = 550;
    var svg = d3
      .select(svgChart.current)
      .attr("width", width)
      .attr("height", height);

    let chartWidth = width - 70;
    const firstToken = parseInt(window.localStorage.getItem("first_token"));
    const secondToken = parseInt(window.localStorage.getItem("second_token"));

    const chunk_size = Math.max(firstToken, secondToken) / 300;
    const getXAxisDataLength = (value) => {
      const getPercentWidth = (chartWidth * 100) / (chunk_size + 1);
      return (parseFloat(value) * getPercentWidth) / 100;
    };

    svg
      .append("g")
      .attr("id", "firstchart")
      .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

    //gap between two charts
    margin.top += 320;

    svg
      .append("g")
      .attr("id", "secondchart")
      .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

    var x1 = d3.scaleBand().range([0, chartWidth]);
    var x2 = d3.scaleBand().range([0, chartWidth]);
    var y1 = d3.scaleLinear().range([150, 0]).domain([0, 300]);
    var y2 = d3.scaleLinear().range([150, 0]).domain([300, 0]);
    var x1Axis = d3.axisBottom(x1);
    var x2Axis = d3.axisTop(x2);
    var y1Axis = d3.axisLeft(y1).ticks(5);
    var y2Axis = d3.axisLeft(y2).ticks(5);

    //populate first chart x Axis in the first chart
    d3.select("#firstchart")
      .append("g")
      .attr("class", "x1 axis")
      .attr("transform", "translate(0," + 150 + ")")
      .call(x1Axis)
      .selectAll("text")
      .attr("y", 0)
      .attr("x", 15)
      .attr("dy", ".10em")
      .attr("transform", "rotate(90)")
      .style("text-anchor", "start");

    //populate second chart x Axis in the second chart
    d3.select("#secondchart")
      .append("g")
      .attr("class", "x2 axis")
      .attr("transform", "translate(0," + 0 + ")")
      .call(x2Axis)
      .selectAll("text")
      .attr("y", -3)
      .attr("x", 15)
      .attr("dy", ".30em")
      .attr("transform", "rotate(-90)")
      .style("text-anchor", "start");

    //populate first chart  y Axis in the first chart
    d3.select("#firstchart")
      .append("g")
      .attr("class", "y1 axis")
      .call(y1Axis)
      .append("text")
      .attr("transform", "rotate(-90)")
      .attr("y", 2)
      .attr("dy", ".31em")
      .style("text-anchor", "end")
      .text("seq1");

    //populate second chart  with y Axis
    d3.select("#secondchart")
      .append("g")
      .attr("class", "y2 axis")
      .call(y2Axis)
      .append("text")
      .attr("transform", "rotate(-90)")
      .attr("y", 2)
      .attr("dy", ".31em")
      .style("text-anchor", "start")
      .text("seq1");

    d3.select("#firstchart")
      .selectAll(".bar")
      .data(data)
      .enter()
      .append("rect")
      .attr("class", "bar")
      .attr("x", function (d) {
        return getXAxisDataLength(d.seq1);
      })
      .attr("width", 0.5)
      .attr("y", function (d) {
        return y1(d.ew1);
      })
      .attr("height", function (d) {
        return y1(d.bw1) - y1(d.ew1);
      })
      .on("mouseover", handleMouseOver)
      .on("mouseout", handleMouseOut);

    // fist chart first marking label
    d3.select("#secondchart")
      .append("text")
      .attr("x", -160)
      .attr("y", 0)
      .attr("fill", "black")
      .attr("z-index", 999)
      .attr("opacity", 1)
      .attr("font-size", "11px")
      .attr("text-anchor", "start")
      .attr("transform", "rotate(90)")
      .text(1);

    // fist chart second marking label
    d3.select("#secondchart")
      .append("text")
      .attr("x", -160)
      .attr("y", -getXAxisDataLength(firstToken / 300))
      .attr("fill", "black")
      .attr("z-index", 999)
      .attr("opacity", 1)
      .attr("font-size", "11px")
      .attr("text-anchor", "start")
      .attr("transform", "rotate(90)")
      .text(parseInt(firstToken / 300) + 1);

    // second chart first marking label
    d3.select("#secondchart")
      .append("text")
      .attr("x", -11)
      .attr("y", 0)
      .attr("fill", "black")
      .attr("z-index", 999)
      .attr("opacity", 1)
      .attr("font-size", "11px")
      .attr("text-anchor", "end")
      .attr("transform", "rotate(90)")
      .text(1);

    // second chart second marking label
    d3.select("#secondchart")
      .append("text")
      .attr("x", -11)
      .attr("y", -getXAxisDataLength(secondToken / 300))
      .attr("fill", "black")
      .attr("z-index", 999)
      .attr("opacity", 1)
      .attr("font-size", "11px")
      .attr("text-anchor", "end")
      .attr("transform", "rotate(90)")
      .text(parseInt(secondToken / 300) + 1);

    // marking fist chart first
    d3.select("#firstchart")
      .append("rect")
      .attr("class", "marking")
      .attr("x", 0)
      .attr("width", 4)
      .attr("y", 0)
      .attr("height", 150)
      .attr("fill", "black");

    // marking fist chart last
    d3.select("#firstchart")
      .append("rect")
      .attr("class", "marking")
      .attr("x", getXAxisDataLength(firstToken / 300))
      .attr("width", 4)
      .attr("y", 0)
      .attr("height", 150)
      .attr("fill", "black");

    // marking second first
    d3.select("#secondchart")
      .append("rect")
      .attr("class", "marking")
      .attr("x", 0)
      .attr("width", 4)
      .attr("y", 0)
      .attr("height", 150)
      .attr("fill", "black");

    // marking second last
    d3.select("#secondchart")
      .append("rect")
      .attr("class", "marking")
      .attr("x", getXAxisDataLength(secondToken / 300))
      .attr("width", 4)
      .attr("y", 0)
      .attr("height", 150)
      .attr("fill", "black");

    d3.select("#secondchart")
      .selectAll(".bar")
      .data(data)
      .enter()
      .append("rect")
      .attr("class", "bar")
      .attr("x", function (d) {
        return getXAxisDataLength(d.seq2);
      })
      .attr("width", 0.5)
      .attr("y", function (d) {
        return y2(d.bw2);
      })
      .attr("height", function (d) {
        return y2(d.ew2) - y2(d.bw2);
      })
      .on("mouseover", handleMouseOver)
      .on("mouseout", handleMouseOut);

    // connections
    d3.select("#firstchart")
      .append("g")
      .attr("class", "connections")
      .selectAll("path")
      .data(data)
      .enter()
      .append("path")
      .style("stroke", "#FFCC66")
      .style("fill", "none")
      .attr("d", function (d) {
        return (
          "M " +
          getXAxisDataLength(d.seq1) +
          " 150 C " +
          getXAxisDataLength(d.seq1) +
          " 250," +
          getXAxisDataLength(d.seq2) +
          " 220 , " +
          getXAxisDataLength(d.seq2) +
          " " +
          320
        );
      })
      .on("mouseover", handleMouseOver)
      .on("mouseout", handleMouseOut);

    function handleMouseOver(event, data) {
      const getAllBar = d3.selectAll(".bar")._groups[0];
      const getBar = [...getAllBar].filter((d) => {
        return d.__data__ === data;
      });

      getBar.map((item) => {
        return d3.select(item).style("stroke", "red").style("stroke-width", 1);
      });

      const getAllPath = d3.selectAll("path")._groups[0];
      const getPath = [...getAllPath].filter((d) => {
        if (d.__data__) {
          return d.__data__ === data;
        } else {
          return null;
        }
      });

      getPath.map((item) => {
        return d3
          .select(item)
          .style("stroke", "#ff9600")
          .style("stroke-width", 2);
      });
    }

    function handleMouseOut(event, data) {
      const getAllBar = d3.selectAll(".bar")._groups[0];
      const getBar = [...getAllBar].filter((d) => {
        return d.__data__ === data;
      });

      getBar.map((item) => {
        return d3.select(item).style("stroke-width", 0);
      });

      const getAllPath = d3.selectAll("path")._groups[0];
      const getPath = [...getAllPath].filter((d) => {
        if (d.__data__) {
          return d.__data__ === data;
        } else {
          return null;
        }
      });

      getPath.map((item) => {
        return d3
          .select(item)
          .style("stroke", "#FFCC66")
          .style("stroke-width", 1);
      });
    }
  }, [data]);

  return (
    <Box sx={{ p: "30px" }}>
      <Box ref={chartContainer} sx={{ width: "100%" }}>
        <svg ref={svgChart}></svg>
      </Box>
    </Box>
  );
};

export default Visual;
