import { Box } from "@mui/material";
import ArrowDropUp from "@mui/icons-material/ArrowDropUp";
import ArrowDropDown from "@mui/icons-material/ArrowDropDown";
import React from "react";

const AuthorDateSort = ({ sortingOrder, handleSortingFilter }) => {
  const getSortValue = () => {
    if (sortingOrder === "text_id__author_id__date") {
      return "-text_id__author_id__date";
    } else if (sortingOrder === "-text_id__author_id__date") {
      return "text_id__author_id__date";
    } else {
      return "text_id__author_id__date";
    }
  };

  return (
    <Box
      display={"flex"}
      flexDirection={"column"}
      alignItems={"center"}
      sx={{ cursor: "pointer" }}
      onClick={() => handleSortingFilter(getSortValue())}
    >
      <ArrowDropUp
        sx={{
          mb: "-8px",
          color:
            sortingOrder === "text_id__author_id__date"
              ? "greenyellow"
              : "white",
        }}
      />
      <ArrowDropDown
        sx={{
          mt: "-8px",
          color:
            sortingOrder === "-text_id__author_id__date"
              ? "greenyellow"
              : "white",
        }}
      />
    </Box>
  );
};

export default AuthorDateSort;
