import React, { useState } from "react";
import { Box, Typography } from "@mui/material";
import BookTitle from "./BookTitle";
import Author from "./Author";
import TableBody from "../TableBody";

const TableHeader = ({ data, toggleSidePanel }) => {
  const [active, setActive] = useState(false);

  return (
    <Box
      display={"flex"}
      flexDirection={"column"}
      justifyContent={"space-between"}
      bgcolor={"#2863A5"}
      color={"white"}
      borderRadius={"5px"}
    >
      <BookTitle data={data} />
      <Author data={data} />
      {active && <TableBody data={data} toggleSidePanel={toggleSidePanel} />}
      <Box
        display={"flex"}
        gap={"4px"}
        alignItems={"center"}
        justifyContent={"flex-end"}
        px={2}
        py={1}
        pb={"6px"}
        onClick={() => setActive(!active)}
        sx={{
          cursor: "pointer",
        }}
      >
        <Typography variant="caption">
          {active ? "See Less" : "See Full Details"}
        </Typography>
        <img
          src="./right-arrow.svg"
          alt="right arrow"
          style={{ width: "12px" }}
        />
      </Box>
    </Box>
  );
};

export default TableHeader;
