import React from "react";
import { getGitHubRepoURL } from "../../../../../utility/Helper";
import YMLIcon from "../../../../../assets/img/yml.png";
import { Box, Typography } from "@mui/material";
import { getAuthorTwentyFiveIncrease } from "../../../../../functions/getAuthorTwentyFiveIncrease";

const BookTitle = ({ data }) => {
  let link = data?.version_uri.split(".")[0]
    ? `https://github.com/OpenITI/${
        getAuthorTwentyFiveIncrease(
          data?.text_id && data?.text_id.author_id.date
        ) < 100
          ? "00"
          : ""
      }${getAuthorTwentyFiveIncrease(
        data?.text_id && data?.text_id.author_id.date
      )}AH/blob/master/data/${
        data?.version_uri.split(".")[0]
      }/${data?.version_uri
        .split(".")
        .slice(0, 2)
        .join(".")}/${data?.version_uri.split(".").slice(0, 2).join(".")}.yml`
    : "";

  return (
    <Box
      display={"flex"}
      flexDirection={"column"}
      pb={2}
      borderBottom={"1px solid #cbd5e1"}
    >
      <Typography
        variant="body2"
        color={"text.primary"}
        textAlign={"right"}
        fontWeight={600}
      >
        Book Title
      </Typography>
      <Typography
        variant="body2"
        textAlign={"right"}
        display={"flex"}
        alignItems={"center"}
        justifyContent={"flex-end"}
        gap={1}
      >
        <a
          href={link}
          target="_blank"
          rel="noreferrer"
          style={{ marginTop: "7px" }}
        >
          <img src={YMLIcon} className="img-icon" alt="yml" />
        </a>
        <a href={getGitHubRepoURL(data?.version_uri, "v")}>
          {data?.text_id &&
            data?.text_id.title_lat.split(" :: ")[0].toUpperCase()}
        </a>
      </Typography>
      <Typography variant="body2" color={"text.primary"} textAlign={"right"}>
        {data?.text_id && data?.text_id.title_ar.split(" :: ")[0]}
      </Typography>
    </Box>
  );
};

export default BookTitle;
