import { Box, Paper } from "@mui/material";
import React from "react";
import TableHeader from "./TableHeader";

const MobileTable = ({ rows, toggleSidePanel }) => {
  return (
    <Box
      display={"flex"}
      flexDirection={"column"}
      gap={2}
      sx={{
        display: {
          xs: "flex",
          md: "none",
        },
      }}
    >
      {rows.map((item, i) => (
        <Box component={Paper} key={i}>
          <TableHeader data={item} toggleSidePanel={toggleSidePanel} />
        </Box>
      ))}
    </Box>
  );
};

export default MobileTable;
