import { useEffect, useState, createContext, useCallback } from "react";
import { getCorpusMetaData } from "../../../services/CorpusMetaData";
import { makeStyles } from "@mui/styles";
import { Grid, Typography } from "@mui/material";
import LeftSidePanel from "../LeftSidePanel";
import { Box } from "@mui/system";
import { useSearchParams } from "react-router-dom";
import PaginationComponent from "../PaginationComponent";
import FilterSidebar from "./FilterSidebar";
import DownloadData from "./DownloadData";
import SearchAndButtons from "../SearchAndButtons";
import TableComponent from "./TableComponent";
import MobileTable from "./MobileTable";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  tableHeaderCell: {
    fontSize: 14,
    backgroundColor: theme.palette.primary.main,
    color: "#fff",
    textAlign: "right",
  },
  tableCell: {
    textAlign: "right",
    verticalAlign: "top",
  },
  gridContainer: {
    [theme.breakpoints.down("sm")]: {
      padding: "20px",
    },
    [theme.breakpoints.up("sm")]: {
      padding: "0px 100px",
    },
  },
}));

export const DrawerDataContext = createContext();
export const DrawerContext = createContext({ setOpen: () => {}, open: false });

const MetadataTable = () => {
  const classes = useStyles();
  const [searchParams, setSearchParams] = useSearchParams();

  // filters element
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [rows, setRows] = useState([]);
  const [status, setStatus] = useState(false);
  const [query, setQuery] = useState("");
  const [sortingOrder, setOrderingOrder] = useState("");
  const [showPrimaryTextOnly, setPrimaryTextOnly] = useState(false);
  const [annotationFilter, setAnnotationFilter] = useState({
    completed: false,
    inProgress: false,
    mARkdown: false,
    notYetAnnotated: false,
    q: "",
  });

  const [totalRecords, setTotal] = useState(0);
  const [isLeftSidePanelOpen, setLeftSidePanel] = useState(false);
  const [versionDetail, setVersionDetails] = useState({});

  // const [authorDetails, setAuthorDetails] = useState({});
  const [showFilters, setFilterPanel] = useState(false);

  const handleChangePage = async (e, newPage) => {
    const params = Object.fromEntries([...searchParams]);
    setSearchParams({ ...params, page: newPage });
  };

  const handleChangeRowsPerPage = (event) => {
    const params = Object.fromEntries([...searchParams]);
    setSearchParams({ ...params, rowsPerPage: +event.target.value });
  };

  const handleFilterPanel = () => {
    setFilterPanel(!showFilters);
  };

  const toggleSidePanel = (val) => {
    setVersionDetails(val);
    setLeftSidePanel(true);
  };

  const handlePrimaryTextToggle = () => {
    setPrimaryTextOnly(!showPrimaryTextOnly);
    const params = Object.fromEntries([...searchParams]);
    if (!showPrimaryTextOnly === true) {
      setSearchParams({ ...params, status: "pri" });
    } else {
      searchParams.delete("status");
      setSearchParams(searchParams);
    }
  };

  const handleSortingFilter = (value) => {
    setOrderingOrder(value);
    const params = Object.fromEntries([...searchParams]);
    setSearchParams({ ...params, ordering: value });
  };

  const updatePrimaryText = useCallback(() => {
    if (searchParams.get("status") === "pri") {
      setPrimaryTextOnly(true);
    } else {
      setPrimaryTextOnly(false);
    }
  }, [searchParams]);

  useEffect(() => {
    updatePrimaryText();
  }, [updatePrimaryText]);

  const updateOrderingOrder = useCallback(() => {
    if (searchParams.has("ordering")) {
      setOrderingOrder(searchParams.get("ordering"));
    } else {
      setOrderingOrder("");
    }
  }, [searchParams, setOrderingOrder]);

  useEffect(() => {
    updateOrderingOrder();
  }, [updateOrderingOrder]);

  const updatePage = useCallback(() => {
    if (searchParams.has("page")) {
      setPage(parseInt(searchParams.get("page")));
    } else {
      setPage(0);
    }
  }, [searchParams]);

  useEffect(() => {
    updatePage();
  }, [updatePage]);

  const updatePageRow = useCallback(() => {
    if (searchParams.has("rowsPerPage")) {
      setRowsPerPage(parseInt(searchParams.get("rowsPerPage")));
    } else {
      setRowsPerPage(10);
    }
  }, [searchParams]);

  useEffect(() => {
    updatePageRow();
  }, [updatePageRow]);

  const handleResetFilters = () => {
    setQuery("");
    setOrderingOrder("");
    setOrderingOrder("");
    setPrimaryTextOnly(false);
    setPage(1);
    setRowsPerPage(10);
    setAnnotationFilter({
      completed: false,
      inProgress: false,
      mARkdown: false,
      notYetAnnotated: false,
      q: "",
    });
    setSearchParams({});
  };

  useEffect(() => {
    setStatus("loading");
    const getData = async () => {
      const data = await getCorpusMetaData(
        page,
        rowsPerPage,
        query,
        annotationFilter["q"],
        sortingOrder,
        showPrimaryTextOnly
      );
      setRows(data.results);
      setStatus("loaded");
      setTotal(data.count);
    };

    getData();
  }, [
    page,
    rowsPerPage,
    query,
    annotationFilter,
    sortingOrder,
    showPrimaryTextOnly,
  ]);

  return (
    <>
      <Grid container className={classes.gridContainer}>
        <Typography variant="h5" mb={2} width={"100%"}>
          Corpus Metadata
        </Typography>

        <FilterSidebar
          showFilters={showFilters}
          handlePrimaryTextToggle={handlePrimaryTextToggle}
          showPrimaryTextOnly={showPrimaryTextOnly}
          annotationFilter={annotationFilter}
          setAnnotationFilter={setAnnotationFilter}
        />

        <Grid item md={showFilters ? 10 : 12} xs={12}>
          <Box sx={{ width: "100%", overflow: "hidden" }}>
            <Grid>
              <SearchAndButtons
                handleResetFilters={handleResetFilters}
                handleFilterPanel={handleFilterPanel}
                getQuery={(q) => {
                  setQuery(q);
                }}
              />
              <Grid container>
                <Grid
                  item
                  container
                  sm={1}
                  md={3}
                  sx={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                  }}
                >
                  <Grid item sm={12} md={6}>
                    <Typography variant="caption" display="block" align="left">
                      Total number of results: {totalRecords}
                    </Typography>
                  </Grid>
                  <Grid item sm={12} md={6}>
                    <DownloadData data={rows} status={status} />
                  </Grid>
                </Grid>

                <Grid item sm={1} md={9}>
                  <PaginationComponent
                    totalRecords={totalRecords}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    handleChangePage={handleChangePage}
                    handleChangeRowsPerPage={handleChangeRowsPerPage}
                  />
                </Grid>
              </Grid>

              <MobileTable rows={rows} toggleSidePanel={toggleSidePanel} />

              <TableComponent
                handleSortingFilter={handleSortingFilter}
                toggleSidePanel={toggleSidePanel}
                rows={rows}
                classes={classes}
                sortingOrder={sortingOrder}
              />

              <PaginationComponent
                totalRecords={totalRecords}
                rowsPerPage={rowsPerPage}
                page={page}
                handleChangePage={handleChangePage}
                handleChangeRowsPerPage={handleChangeRowsPerPage}
              />
            </Grid>
          </Box>
        </Grid>
      </Grid>
      <DrawerDataContext.Provider value={{ versionDetail }}>
        <DrawerContext.Provider
          value={{
            close: () => setLeftSidePanel(false),
            isOpen: isLeftSidePanelOpen,
          }}
        >
          <LeftSidePanel />
        </DrawerContext.Provider>
      </DrawerDataContext.Provider>
    </>
  );
};
export default MetadataTable;
