import React from "react";
import { Box, ButtonGroup, IconButton, Typography } from "@mui/material";
import DownLoadDropdown from "../../../DownLoadDropdown";
import MoreHorizIcon from "@mui/icons-material/MoreHoriz";

const MoreAction = ({ data, toggleSidePanel }) => {
  return (
    <Box
      display={"flex"}
      flexDirection={"column"}
      borderBottom={"1px solid #cbd5e1"}
      pb={2}
    >
      <Typography
        variant="body2"
        color={"text.primary"}
        textAlign={"right"}
        fontWeight={600}
      >
        More Actions
      </Typography>
      <Typography
        variant="body2"
        textAlign={"right"}
        display={"flex"}
        alignItems={"center"}
        justifyContent={"flex-end"}
        gap={1}
      >
        <ButtonGroup variant="outlined" aria-label="outlined button group">
          <IconButton
            onClick={() => {
              toggleSidePanel(data);
            }}
            size={"small"}
            color={"neutral"}
          >
            <MoreHorizIcon />
          </IconButton>
          <DownLoadDropdown versionURI={data?.version_uri} isMobile />
        </ButtonGroup>
      </Typography>
    </Box>
  );
};

export default MoreAction;
